import { handleFollow, cancelFollow, handleFavortie, cancelFavortie, deleteArticle } from '@/api/article'
export default {
    name: "articleInfo",
    props: {
        article: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            articleInfo: null,
            deleteLoading: false
        }
    },
    created() {
      this.articleInfo = this.article
    },
    methods: {
       async handleFollow(article) {
            if (!article.author.following) {
                await handleFollow({
                    username: article.author.username
                })
                article.author.following = true
            }else {
                await cancelFollow({
                    username: article.author.username
                })
                article.author.following = false
            }
        },
        async handleFavorite(article) {
            article.favoriteDisabled = true
            if (!article.favorited) {
                await handleFavortie({article})
                article.favorited = true
                article.favoritesCount += 1
            }else {
                await cancelFavortie({article})
                article.favorited = false
                article.favoritesCount -= 1
            }
            article.favoriteDisabled = false
        },
        async delArticle() {
            this.deleteLoading = true
            await deleteArticle({
                slug: this.article.slug
            })
            this.deleteLoading = false
            this.$router.push({
                name: 'profile',
                params: {
                    username: this.article.author.username
                },
                query: {
                    isMyself: 'true',
                    tab: 'my_article'
                }
            })
        }
    }
}
