import { addComment, queryComment } from '@/api/article.js'
export default {
    name: "articleComment",
    data() {
        return {
            articleComment: [],
            comment: '',
            loading: false
        }
    },
    mounted() {
        this.getComment()
    },
    methods: {
        async addComment() {
            this.loading = true
            await addComment({
                data: {
                    comment: {
                        body: this.comment,
                    }
                },
                slug: this.$route.params.slug
            })
            this.loading = false
            this.getComment()
            // console.log('this.$router', this.$route.params.slug)
        },
        async getComment() {
            const { data } = await queryComment({slug: this.$route.params.slug})
            this.articleComment = data.comments
        }
    }
}
